<template>
	<za-page-content header="标签管理" scroll="true">
		<za-table ref="table" :data="list" @action-change="changePage" highlight-current-row :total="total" @selection-change="handleSelectionChange">
			<div slot="searchBar">
				<el-form :model="filters" ref="filtersForm">
					<el-form-item>
						<el-col :span="6">
							<el-col :span="8">审核状态:</el-col>
							<el-col :span="16">
								<el-select style="width:100%" v-model="filters.authStatus" placeholder="请选择">
									<el-option v-for="item in authStatusList" :key="item.label" :label="item.label" :value="item.value">
									</el-option>
								</el-select>
							</el-col>
						</el-col>

						<el-col :span="7" :offset="1">
							<el-col :span="8">标签查询:</el-col>
							<el-col :span="16">
								<el-input v-model="filters.labelName" placeholder="搜索标签..." :clearable="true"></el-input>
							</el-col>
						</el-col>

						<el-col :span="4" :offset="1">
							<el-col :span="16">
								<el-checkbox @change="changeRecommend">运营推荐</el-checkbox>
							</el-col>
						</el-col>

						<el-col :span="4">
            	<el-button icon="el-icon-search" type="primary" @click="getList">查询</el-button>
						</el-col>

					</el-form-item>
				</el-form>
			</div>

			<el-row slot="searchBar">
				<el-button style="margin-top: 20px;" size="medium" @click="addNewTag" type="primary">添加标签</el-button>
				<el-button style="margin-top: 20px;" size="medium" @click="batchAction('pass')" type="success">批量审核</el-button>
				<el-button style="margin-top: 20px;" size="medium" @click="batchAction('del')" type="danger">批量删除</el-button>
			</el-row>

			<el-table-column type="selection" width="50"></el-table-column>
			<el-table-column prop="labelName" label="标签"></el-table-column>
			<el-table-column label="审核状态">
				<template slot-scope="scope">
					{{scope.row.authStatus === '1' ? '已审核' : '未审核'}}
				</template>
			</el-table-column>
			<el-table-column label="是否运营推荐" width="120px">
				<template slot-scope="scope">
					{{scope.row.isRecommend === 'Y' ? '是' : '否'}}
				</template>
			</el-table-column>
			<el-table-column label="操作" width="220px">
				<template slot-scope="scope">
					<el-button size="mini" type="success" @click="shenheTag(scope.row)">审核</el-button>
					<el-button size="mini" type="primary" @click="editTag(scope.row)">编辑</el-button>
					<el-button size="mini" type="danger" @click="deleteTag(scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</za-table>
		<el-dialog :title="dialogType === 'add' ? '添加标签': '编辑标签'" :visible.sync="dialogVisiblePage" @close="hideDialog" width="500px">
			<el-row>
				<el-row>
					<el-form :model="subEdit" ref="form" :rules="formValidate" label-position="left" label-width="120px">
						<el-row type="flex" align="middle">
							<span class="tags-trip" v-show="dialogType==='add'">注：多个标签使用英文“,”隔开</span>
							<el-form-item label="标签" prop="labelName" style="width: 100%">
								<el-col :span="20">
									<el-input v-model="subEdit.labelName"></el-input>
								</el-col>
							</el-form-item>
						</el-row>
						<el-row type="flex" align="middle">
							<el-form-item label="是否推荐给用户" prop="isRecommend" style="width: 100%">
								<el-col :span="20">
									<el-radio v-model="subEdit.isRecommend" label="Y">是</el-radio>
									<el-radio v-model="subEdit.isRecommend" label="N">否</el-radio>
								</el-col>
							</el-form-item>
						</el-row>
					</el-form>
				</el-row>
				<div class="dialog-footer" style="text-align: center">
					<el-button @click="cancelDialog">取 消</el-button>
					<el-button @click="saveTag" type="primary">确 定</el-button>
				</div>
			</el-row>
		</el-dialog>
	</za-page-content>
</template>

<script>
import { ajaxGetCommon, ajaxPostCommon } from "../../api/common.js";
import * as ajaxUrl from "../../api/url";

export default {
  data() {
    return {
      formValidate: {
        labelName: { required: true, message: "请输入标签", trigger: "blur" },
        targetUrl: {
          required: true,
          message: "请输入爬取目标url",
          trigger: "blur"
        },
        targetType: {
          required: true,
          message: "请输入目标类型",
          trigger: "blur"
        },
        directoryId: {
          required: true,
          message: "请输入栏目编号",
          trigger: "blur"
        }
      },
      authStatusList: [
        { label: "全部", value: "" },
        { label: "未审核", value: "0" },
        { label: "已审核", value: "1" }
      ],
      filters: {
        authStatus: "",
        labelName: "",
        isRecommend: ""
      },
			selectionRowData: [],
			dialogType: '',
      dialogVisiblePage: false,
      subEdit: {
        labelName: "",
        isRecommend: "N"
      },
      labelId: null,
      cate: null,
      list: [],
      total: 1,
      pageNum: 1,
      pageSize: 10
    };
  },
  mounted() {
    this.getList();
  },
  methods: {
    getOption() {
      ajaxPostCommon(ajaxUrl.pageInfoLabelCategory, {
        pageNum: this.pageNum,
        pageSize: this.pageSize
      }).then(data => {
        console.log(data);
        if (data.returnCode === "200") {
          this.options = this.formateCate(data.result.list);
          this.cate = this.options[0].value;
          this.getList();
        }
      });
    },
    formateCate(data) {
      let res = [];
      data.forEach(item => {
        let obj = {
          value: item.labelCategoryId,
          label: item.labelCategoryName
        };
        res.push(obj);
      });
      console.log(res);
      return res;
    },
    getList() {
      ajaxPostCommon(ajaxUrl.pageInfoLabel, {
        pageNum: this.pageNum,
        pageSize: this.pageSize,
        ...this.filters
      }).then(data => {
        if (data.returnCode === "200") {
          this.list = data.result.list;
          this.total = data.result.total;
        }
      });
    },
    changeCate() {
      this.getList();
    },
    editTag(row) {
			this.dialogType = 'edit';
      ajaxPostCommon(ajaxUrl.getInfoLabelDetail, { labelId: row.id }).then(
        data => {
          if (data.returnCode === "200") {
            let nowData = data.result;
            this.subEdit = {
              labelName: nowData.labelName,
              isRecommend: nowData.isRecommend
            };
            this.openDialog();
            this.labelId = nowData.labelId;
          }
        }
      );
    },
    saveTag() {
      let validatePass = false;
      this.$refs.form.validate(pass => {
        console.log(pass);
        if (pass) validatePass = true;
      });
      if (!validatePass) {
        return false;
      }
      let params = {
        labelName: this.subEdit.labelName,
        isRecommend: this.subEdit.isRecommend
      };
      if (this.labelId) {
        this.updateTag(params);
        return;
      }
      ajaxPostCommon(ajaxUrl.saveInfoLabel, params).then(data => {
        if (data.returnCode === "200") {
          this.cancelDialog();
          this.$message({
            message: "保存成功~",
            type: "success"
          });
          this.getList();
        }
      });
    },
    updateTag(params) {
      let nowPar = Object.assign(params, { labelId: this.labelId });
      ajaxPostCommon(ajaxUrl.updateInfoLabel, nowPar).then(data => {
        if (data.returnCode === "200") {
          this.cancelDialog();
          this.$message({
            message: "保存成功~",
            type: "success"
          });
          this.getList();
        }
      });
    },
    deleteTag(row) {
      this.$confirm("确定删除选中标签么?", "提示", {})
        .then(() => {
          ajaxPostCommon(ajaxUrl.deleteInfoLabel, { labelId: row.id }).then(
            data => {
              if (data.returnCode === "200") {
                this.$message({
                  message: "删除成功~",
                  type: "success"
                });
                this.getList();
              } else {
                this.$message({
                  message: data.returnMsg,
                  type: "error"
                });
              }
            }
          );
        })
        .catch(() => {});
    },
    batchAction(type) {
      let tags = "",
        tagsArray = [],
        service = null,
        confirmMsg = "",
        successMsg = "";
      this.selectionRowData.forEach(i => {
        tagsArray.push(i.id);
      });
      tags = tagsArray.join(",");
      if (!tags) {
        this.$message({
          showClose: true,
          message: "请选择标签",
          type: "error"
        });
        return false;
      }

      if (type === "pass") {
        service = ajaxUrl.batchPassTags;
        confirmMsg = "确定审核通过?";
        successMsg = "通过成功";
      } else {
        service = ajaxUrl.batchDelTags;
        confirmMsg = "确定删除?";
        successMsg = "删除成功";
      }

      this.$confirm(confirmMsg, "提示", {}).then(() => {
        ajaxPostCommon(service, { list: tags }).then(res => {
          if (res.returnCode == 200) {
            this.$message({
              showClose: true,
              message: successMsg,
              type: "success"
            });
            this.getList();
          } else {
            this.$message({
              showClose: true,
              message: res.returnMsg,
              type: "error"
            });
          }
        });
      });
    },
    shenheTag(row) {
      this.$confirm("确定审核选中标签么?", "提示", {})
        .then(() => {
          ajaxPostCommon(ajaxUrl.updateAuthStatus, { labelId: row.id }).then(
            data => {
              if (data.returnCode === "200") {
                this.$message({
                  message: "审核成功~",
                  type: "success"
                });
                this.getList();
              } else {
                this.$message({
                  message: data.returnMsg,
                  type: "error"
                });
              }
            }
          );
        })
        .catch(() => {});
    },
    addNewTag() {
      this.subEdit = {
        labelName: "",
        isRecommend: "N"
      };
			this.labelId = null;
			this.dialogType = "add";
      this.openDialog();
    },
    openDialog() {
      this.dialogVisiblePage = true;
    },
    hideDialog() {
      this.$refs.form.clearValidate();
    },
    cancelDialog() {
      this.dialogVisiblePage = false;
    },
    changePage(params) {
      this.pageNum = params.page;
      this.pageSize = params.rows || 10;
      this.getList();
    },
    handleSelectionChange(selection) {
      this.selectionRowData = selection;
    },
    changeRecommend(v) {
      this.filters.isRecommend = !!v ? "Y" : "";
    }
  }
};
</script>

<style lang="scss" scoped>
.targetTit {
  margin-bottom: 20px;
}

.tag-tit {
  margin-bottom: 0px;
  padding-bottom: 20px;
  border-bottom: 1px solid #efefef;
}
.tags-trip {
	position: absolute;
	left: 120px;
	bottom: 0;
	font-size: 12px;
	color: red;
}
</style>



